export function priceLine(target, params) {
    const subject = target.createPriceLine(params);
    return {
        subject() {
            return subject;
        },
        update(nextParams) {
            if (nextParams) {
                subject.applyOptions(nextParams);
            }
        },
        destroy() {
            target.removePriceLine(subject);
        }
    };
}
